(function() {
    function layoutCtrl($scope, $timeout) {
        'ngInject';
        $scope.remote = false;
        $scope.active = 1;
        $scope.now = '';
        var timer;
        getNow();

        function getNow() {
            $scope.now = moment().format('YYYY年MM月DD日');
            $scope.time = moment().format('HH:mm:ss') + ' ' + moment().format('dddd');
            if (timer) $timeout.cancel(timer);
            timer = $timeout(function() {
                getNow();
            }, 1000);
        }
    }

    angular.module('app.layout').controller('layoutCtrl', layoutCtrl);
})();

'use strict';
angular.module('app').directive('ngCompileHtml', function ($compile) {
  return function (scope, element, attrs) {
    var unbindWatch = scope.$watch(
      function (scope) {
        // watch the 'compile' expression for changes
        return scope.$eval(attrs['ngCompileHtml']);
      },
      function (value) {
        // when the 'compile' expression changes
        // assign it into the current DOM
        element.html(value);

        // compile the new DOM and link it to the current
        // scope.
        // NOTE: we only compile .childNodes so that
        // we don't get into infinite loop compiling ourselves
        $compile(element.contents())(scope);
      }
    );
    scope.$on('$destroy', function () {
      unbindWatch();
    })
  };
});

